        EQHandshakeBypass: false,
        CST_READER_TRIGGER: false,
        ForkLifer_Enable: false,
        LDULD_Task_No_Entry: false,
        LastVisitedTag: 17,
        ForbidToOnlineTags: [
          49
        ],
        CutOffChargeRelayCurrentThreshodlval: 5000,
        LDULD_Laser_Mode: 0,
        Spin_Laser_Mode: 5,
        LDULD_FrontBackLaser_Bypass: true,
        Connections: {
          RosBridge: {
            IP: "192.168.235.130",
            Port: 9090
          },
          Wago: {
            IP: "127.0.0.1",
            Port: 9999
          },
          AGVS: {
